
import React, { Component } from 'react';




class forHire extends Component{


    componentDidMount() {
        this.initViz()
    }


    initViz() {
        const vizUrl= 'https://public.tableau.com/views/FHVYELLOW2020JAN/Dashboard1';
        const vizContainer = this.vizContainer;
        let viz = new window.tableau.Viz(vizContainer, vizUrl)
    }


    render() {
        return (
            <div className='taxiData' ref={(div) => { this.vizContainer = div }}>
            </div>
        )
    }
}


export default forHire;