import React from 'react';
import {connect} from "react-redux";
import { Layout, Menu } from 'antd';
import {Link, Route, Switch, withRouter} from 'react-router-dom';
import { BarChartOutlined, DotChartOutlined, MenuFoldOutlined, HomeOutlined, LineChartOutlined, HomeTwoTone } from '@ant-design/icons'
import { NavItem, NavLink } from 'reactstrap';
import type { AuthState } from '../../data/modules/auth';
import { logout } from '../../data/modules/auth';
import MainPage from "../Home";
import SearchBar from "../searchBar";
import SignIn from "../signin";
import SignUp from "../signup";
import TableauTaxiAndWeatherDb from "../tableauDashboard/taxiAndWeather";
import TableauTaxiDb from "../tableauDashboard/taxi";
import '../../styles/AppNav.css';
import forHire from "../tableauDashboard/forHire";


const { Header, Content, Sider } = Layout;

    type Props = {
    auth: AuthState,
    logout: () => void,
};

type State = {
    collapsed: false,
    subscriptionActive: boolean
}



 class AppNav extends React.Component<Props, State> {
     props: Props;
     state: State;

     constructor(props) {
         super(props);
         this.state = {
             subscriptionActive: false,
             collapsed: false
         };
     }

     toggle = () => {
         this.setState({
             collapsed: !this.state.collapsed,
         });
     };

     authLink(signedIn, username) {
         if (!signedIn) {
             return (
                 <div>
                     <span><Link className="signIn" to="/signin">Log In</Link></span>
                     <span><Link className="signIn" to="/signup">Sign Up</Link></span>
                 </div>
             )
         }
         return (
             <span>
                  {/*<NavItem>*/}
                  {/*       <NavLink><div className="text-info">{username}</div></NavLink>*/}
                  {/*</NavItem>*/}
                 <a className="signIn" href='#' onClick={() => this.loggingOut()}>Sign Out</a>
             </span>
         );
     }

     loggingOut() {
         this.props.logout();
         this.props.history.push("/");
     }


     forLoginUser(signedIn) {
         if (signedIn) {
             return (
                 <div>
                     <Menu mode="inline" defaultSelectedKeys={['1']}>
                         <Menu.Item key="1">
                         <HomeOutlined />
                         <span>Home</span>
                         <Link to="/"></Link>
                     </Menu.Item>
                         <Menu.Item key="2">
                             <DotChartOutlined />
                             <span>Taxi And Weather Data</span>
                             <Link to="/taxiAndWeather"></Link>
                         </Menu.Item>
                         <Menu.Item key="3">
                             <BarChartOutlined />
                             <span>Taxi Data Only</span>
                             <Link to="/taxi"></Link>
                         </Menu.Item>
                         <Menu.Item key="4">
                             <LineChartOutlined />
                             <span>For-Hired Vehicle Data</span>
                             <Link to="/forhire"></Link>
                         </Menu.Item>


                     </Menu>
                 </div>
             )
         }
     }




     render() {
         const { signedIn, username } = this.props.auth;
         // const signedIn = true;

        return (
            <Layout>
                <Sider theme="light" trigger={null} collapsible collapsed={this.state.collapsed}>
                    <MenuFoldOutlined
                        className="trigger"
                        type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
                        onClick={this.toggle}
                    />
                    {this.forLoginUser(signedIn)}

                </Sider>
                <Layout>
                    <Header className="topNavBar">
                        <div><span><Link className="home" to="/"> Home </Link></span></div>
                        <SearchBar/>
                        <div className="grow" />
                        <div className="signIn">{this.authLink(signedIn, username)}</div>
                    </Header>
                    <div className="fullscreen">
                        <Content >
                            <Switch>
                                <Route exact path="/" component={MainPage} />
                                <Route exact path="/signin" component={SignIn} />
                                <Route exact path="/signup" component={SignUp} />
                                <Route exact path="/taxiAndWeather" component={TableauTaxiAndWeatherDb} />
                                <Route exact path="/taxi" component={TableauTaxiDb} />
                                <Route exact path="/forhire" component={forHire} />
                            </Switch>
                        </Content>
                    </div>
                </Layout>
            </Layout>

        );
    }
}

const mapStateToProps = state => ({
    auth: state.auth,
});
const mapDispatchToProps = { logout };

export default withRouter(
    connect(mapStateToProps, mapDispatchToProps)(AppNav)
);


