import axios from 'axios';
import type {Thunk} from '../';
import {authenticated, login} from '../modules/auth';
import * as Names from '../../constants/names';



export type RegState = {
    registered: boolean,
    regFailure: boolean,
    regErrType: string,
    username: string,


};

type RegisterAction = {
    type: 'REGISTERED' | 'REGISTER_FAILURE',
    data: {
        registered: boolean,
        regFailure: boolean,
        regErrType: string,
        username: string,

    }
};


type Action = RegisterAction;

const defaultState = {
    registered: false,
    regFailure: false,
    regErrType: '',
    username: '',

};

export default function reducer(state: RegState = defaultState, action: Action): RegState {
    switch (action.type) {
        case 'REGISTERED':
            return {
                registered: true,
                regFailure: false,
                username: action.data.username
            };
        case 'REGISTER_FAILURE':
            return {
                registered: false,
                regFailure: true,
                regErrType: 'User Exist'
            };
        case 'SET_DEFAULT':
            return defaultState;

        default:
            return state;
    }
}

export function userRegistered(name): RegisterAction {
    return {
        type: 'REGISTERED',
        data: {registered: true, regFailure: false, username: name}
    }

}

export function registerFailure(): RegisterAction {
    return {
        type: 'REGISTER_FAILURE',
        data: { registered: false, regFailure: true, regErrType: 'User Exist'}
    }

}

export function setDefault(): RegisterAction{
    return {
        type: 'SET_DEFAULT'
    }

}


export function register(username: string, password: string, email: string): Thunk<RegisterAction> {
    return dispatch => {

        const user_data = {
            email: email,
            lastPasswordResetDate: '',
            password: password,
            username: username};
        console.log("CHECK DATA!!!!");
        console.log(user_data);
        axios.post(`/api/register`, user_data)
            .then(
                success => {
                    dispatch(userRegistered(username));
                    dispatch(login(username, password));
                },
                failure => {
                    dispatch(registerFailure());
                }
            );
    };




}