import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import {  Container, FormGroup, Label, ListGroup, ListGroupItem } from 'reactstrap';
import {Form, Input, Button, Card, Alert} from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { authenticated, login, logout } from '../../data/modules/auth';
import type { AuthState } from '../../data/modules/auth';



type Props = {
    authState: AuthState,
    location: {
        state?: {
            nextPathname?: string
        }
    },
    history: {
        push: (path: string) => void
    },
    login: (username: string, password: string) => void,
    logout: () => void,
};

type State = {
    authFailed: boolean,
    username: string,
    password: string
};

class SignIn extends React.Component<Props, State> {
    state = {
        authFailed: false,
        username: '',
        password: ''
    };
    handleOnSignIn(event) {
        event.preventDefault();

        const { username, password } = this.state;

        const u = username ? username.trim() : '';
        const p = password ? password.trim() : '';

        if (u.length === 0) {
            return;
        }

        this.props.login(u, p);

    }

    handleSignOut(event) {
        event.preventDefault();

        this.props.logout();
    }

    handleChange = (e) => {
        this.setState({ [e.target.name]: e.target.value });
    };

    authFailedMessage() {
        if (!this.props.authState.authFailure) {
            return null;
        }

        return (
            <div>
                <Alert
                    message="Authentication failed!"
                    description="Please Enter a valid username and password."
                    type="error"
                    showIcon
                />
            </div>
        );
    }

    authSucceededMessage() {
        if (this.props.authState.signedIn) {

            return (
                <div>
                    <Alert message="Authentication Succeeded!" type="success" showIcon />
                    <div>Signed in as: {this.props.authState.username}</div>
                </div>
            )
        }

        return null;
    }


    render() {
        const { username, password } = this.state;
        const { authState } = this.props;

        return (
            <div>
                <Container>
                    <br />
                    <h1 id="textH3">Sign In</h1>
                    <Container>
                        {this.authSucceededMessage()}
                        {this.authFailedMessage()}
                    </Container>
                    <Card className="signUpCard" style={{width:500, alignItems:"center", marginTop:20}}>
                        <Form>
                            <FormGroup>
                                <Label for="username">Username</Label>
                                <br />
                                <Input prefix={<UserOutlined style={{ color: 'rgba(0,0,0,.25)' }} />}
                                       className="login-form"
                                       type="username"
                                       name="username"
                                       id="username"
                                       placeholder="Username"
                                       value={username}
                                       onChange={this.handleChange}
                                />
                            </FormGroup>
                            <FormGroup>
                                <Label for="username">Password</Label>
                                <br />
                                <Input prefix={<LockOutlined style={{ color: 'rgba(0,0,0,.25)' }} />}
                                       className="login-form"
                                       type="password"
                                       name="password"
                                       id="password"
                                       placeholder="Password"
                                       value={password}
                                       onChange={this.handleChange}
                                />
                            </FormGroup>

                            <Button disabled={authState.signedIn} onClick={e => this.handleOnSignIn(e)}>Login</Button>{' '}
                            <Button disabled={!authState.signedIn} onClick={e => this.handleSignOut(e)}>Logout</Button>
                        </Form>
                    </Card>
                    <br/>
                </Container>
            </div>

        );
    }
}



function mapStateToProps(state) {
    return {
        authState: state.auth
    };
}

export default withRouter(
    connect(mapStateToProps, { login, logout })(SignIn)
);