import React, { Component } from 'react';




class TableauTaxiDb extends Component{


    componentDidMount() {
        this.initViz()
    }


    initViz() {
        // const vizUrl= 'https://public.tableau.com/views/OpenAQ2020NYTaxi/Dashboard2';
        const vizUrl= 'https://public.tableau.com/shared/9BRTR2XTF?:display_count=y&:origin=viz_share_link';
        // const vizUrl='https://public.tableau.com/shared/H54PTHCZ6';
        const vizContainer = this.vizContainer;
        let viz = new window.tableau.Viz(vizContainer, vizUrl)
    }


    render() {
        return (
            <div className='taxiData' ref={(div) => { this.vizContainer = div }}>
            </div>
        )
    }
}


export default TableauTaxiDb;