import React from 'react';
import { Container } from 'reactstrap';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { RegState, register } from '../../data/modules/register';
import { AuthState } from '../../data/modules/auth';
import { Form, Input, Button, Card, Alert, Result } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';

const formItemLayout = {
    labelCol: {
        xs: { span: 24 },
        sm: { span: 8},
    },
    wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
    },
};
const tailFormItemLayout = {
    wrapperCol: {
        xs: {
            span: 24,
            offset: 0,
        },
        sm: {
            span: 16,
            offset: 8,
        },
    },
};
type Props = {
    regState: RegState,
    authState: AuthState,
    register: (username: string, password: string, email: string) => void,
    history: {
        push: (path: string) => void
    },
};

type State = {
    username: string,
    password: string,
    email: string,

};
class SignUp extends React.Component<Props, State> {
    state = {
        username: '',
        password: '',
        email: '',
        confirmDirty: false,
    };

    handleSignUp(event) {
        event.preventDefault();

        const { username, password, email } = this.state;
        const u = username ? username.trim() : '';
        const p = password ? password.trim() : '';
        const e = email ? email.trim() : '';
        //need check valid input
        this.props.register(u,p,e);
        console.log("register CHECK!!!!!");
        console.log(this.state);
        console.log(this.props);

    }

    handleConfirmBlur = e => {
        const { value } = e.target;
        this.setState({ confirmDirty: this.state.confirmDirty || !!value });
    };




    handleChange = (e) => {
        this.setState({ [e.target.name]: e.target.value });
    };

    regFailedMessage() {
        if (this.props.regState.regFailure) {

            return (
                <Result
                    status="error"
                    title="Submission Failed"
                    subTitle={this.props.regState.regErrType}
                />


            );
        }
        return null;
    }

    regSucceededMessage() {
        if (this.props.regState.registered) {
            return (
               // <div>
                 //   <Alert message="Register and Login Succeeded!" type="success" showIcon />
                //</div>

                <Result
                    status="success"
                    title="Successfully Register Our Website!"
                />
            )
        }
        return null;

    }


    render() {
        const { username, password, email } = this.state;

        return (
            <div>
                <Container>
                    <br />
                    <h1 id="textH3">Register</h1>
                    <Container>
                        {this.regSucceededMessage()}
                        {this.regFailedMessage()}
                    </Container>
                    <Card className="signUpCard" style={{width:500, alignItems:"center", marginTop:20}}>
                        <Form className="register-form" {...formItemLayout}>

                            <br/>
                            <Form.Item name="username"
                                       label="Username"
                                       rules={[
                                           {
                                               pattern: /^[a-z0-9_]+$/,
                                               message: 'The input is not valid username!',
                                           },
                                           {
                                               required: true,
                                               message: 'Please input a username!',
                                           },

                                       ]}>
                                <Input type="username"
                                       name="username"
                                       id="username"
                                       placeholder="Username"
                                       value={username}
                                       onChange={this.handleChange}
                                       prefix={<UserOutlined style={{ color: 'rgba(0,0,0,.25)' }} />}
                                />
                            </Form.Item>
                            <Form.Item name="password"
                                       label="Password"
                                       rules={[
                                           {
                                               required: true,
                                               message: 'Please input your password!',
                                           },
                                           {
                                               min: 6,
                                               message: 'must be at least 6 characters',
                                           }
                                       ]}
                                       hasFeedback >
                                <Input.Password
                                    name="password"
                                    id="password"
                                    placeholder="Password"
                                    value={password}
                                    onChange={this.handleChange}
                                    prefix={<LockOutlined style={{ color: 'rgba(0,0,0,.25)' }} />}
                                />
                            </Form.Item>
                            <Form.Item name="confirm"
                                       label="Confirm Password"
                                       dependencies={['password']}
                                       hasFeedback
                                       rules={[
                                           {
                                               required: true,
                                               message: 'Please confirm your password!',
                                           },
                                           ({ getFieldValue }) => ({
                                               validator(_, value) {
                                                   if (!value || getFieldValue('password') === value) {
                                                       return Promise.resolve();
                                                   }
                                                   return Promise.reject(new Error('The two passwords that you entered do not match!'));
                                               },
                                           }),
                                       ]}>
                                <Input.Password
                                    placeholder="Confirm Password" onBlur={this.handleConfirmBlur}
                                    prefix={<LockOutlined style={{ color: 'rgba(0,0,0,.25)' }} />}
                                />
                            </Form.Item>
                            <Form.Item name="email"
                                       label="E-mail"
                                       rules={[
                                           {
                                               type: 'email',
                                               message: 'The input is not valid E-mail!',
                                           },
                                           {
                                               required: true,
                                               message: 'Please input your E-mail!',
                                           },
                                       ]}>
                                <Input type="Email"
                                       name="email"
                                       id="Email"
                                       placeholder="Email"
                                       value={email}
                                       onChange={this.handleChange}
                                       prefix={<UserOutlined style={{ color: 'rgba(0,0,0,.25)' }} />}
                                />
                            </Form.Item>

                            <br />
                            <Form.Item {...tailFormItemLayout}>
                                <Button onClick={e => this.handleSignUp(e)}> Register</Button>
                                <Button
                                    disabled={!this.props.regState.registered}
                                    onClick={e => this.props.history.push("/taxiAndWeather")}>Finish</Button>
                            </Form.Item>
                        </Form>
                    </Card>

                </Container>
            </div>
        );
    }
}

// const SignUp = Form.useForm({ name: 'register' })(RegistrationForm);
function mapStateToProps(state) {
    console.log(state);
    return {
        regState: state.register,
        authState: state.auth
    };
}

export default withRouter(
    connect(mapStateToProps, { register })(SignUp)
);
